<template>
	<div class="articles_list">
		<h2></h2>
		<el-table :data="items" border >
			<el-table-column prop="_id" label="ID" width="230">
			</el-table-column>
			<el-table-column prop="title" label="">
			</el-table-column>
			<el-table-column fixed="right" label="" width="180">
				<template slot-scope="scope">
					<el-button 
            @click="$router.push(`/articles/create/${scope.row._id}`)" 
            type="primary" 
            size="small"
          ></el-button>
					<el-button @click="remove(scope.row)" type="primary" size="small"></el-button>
				</template>
			</el-table-column>
		</el-table>
	</div>
</template>

<script>
	export default {
    name: 'ArticleList',
    data() {
      return {
        items: []
      }
    },
    created() {
      this.fetch()
    },
    methods: {
      // 
      async fetch() {
        const res = await this.$http.get('rest/articles')
        if (res.status !== 200) return this.$message.error('')
        this.items = res.data
      },
      async remove(row) {
        const confirmResult = await this.$confirm(
          ` "${row.title}"`,
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        ).catch(err => err)
        if (confirmResult !== 'confirm') return this.$message.info('')
        const res = await this.$http.delete(`rest/articles/${row._id}`)
        this.$message.success('')
        this.fetch()
      },
    }
	}
</script>

